/* Fix for white text on white background */

body {
  background-color: #f5f7fa !important;
  color: #333 !important;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f7fa !important; /* Light gray background */
  color: #333 !important; /* Dark text color */
  min-height: 100vh;
}

.App {
  min-height: 100vh;
}

/* Ensure all text is visible */
h1, h2, h3, h4, h5, h6, p, div, span, a {
  color: #333 !important;
}

/* Links */
a {
  color: #3498db !important;
  text-decoration: none;
}

a:hover {
  color: #2980b9 !important;
  text-decoration: underline;
}

/* Cards and sections */
.card, section, article, div[style*="background"] {
  background-color: white !important;
  color: #333 !important;
}

/* Navbar */
nav {
  background-color: #2c3e50 !important;
  color: white !important;
}

nav a {
  color: white !important;
}

/* Footer */
footer {
  background-color: #2c3e50 !important;
  color: white !important;
}

footer a {
  color: #3498db !important;
}

/* Force text visibility */
* {
  color: inherit !important;
}

/* Override any white text */
.white-text, .text-white, [style*="color: white"], [style*="color:#fff"], [style*="color:#ffffff"] {
  color: #333 !important;
}

/* Background fixes */
[style*="background: white"], [style*="background-color: white"] {
  color: #333 !important;
}

